.form-group {
    padding: 20px;

    &__btn-gruop {
        margin-top: 15px;
        text-align: right;
    }

    >.input {
        margin-top: 15px;
    }

    &__btn-left {
        margin-right: 2%;
        background-color: $disabled-color !important;

        &:hover {
            background-color: $disabled-color-light !important;
            border: solid 1px $disabled-color-light !important;

            >span {
                color: $black-color;
            }
        }
    }

    &__btn-right {
        background-color: $primary-color-bold !important;

        >span {
            color: $white-color;
        }

        &:hover {
            background-color: $purple-color-bold !important;
            border: solid 1px $purple-color-bold !important;
        }
    }

    &__title {
        border-bottom: solid 2px $primary-color-bold;
    }

    &__label-left {
        font-weight: 700;
        margin-bottom: 2%;
        float: left;
    }

    &__label-right {
        margin-bottom: 2%;
        margin-left: 2%;
        float: left;
    }
}

.btn-modal {
    margin-top: 7%;
}

.form {
    &__nick-name {
        margin-top: 10px;

        >.input {
            margin-bottom: 15px;            
        }
        .btn-primary{
            width: 100%;
        }
    }
}